@font-face {
  font-family: robotoMono;
  src: url(./../static/fonts/RobotoMono/RobotoMono-VariableFont_wght.ttf);
}

html {
  font-size: 21px;
  font-family: robotoMono, Arial;
}

@media (max-width: 900px) {
  html { font-size: 19px; }
}

@media (max-width: 450px) {
  html { font-size: 17.5px; }
}

