button {
  border-radius: 0.1em;
  cursor: pointer;
  margin-block: 0.2em;
  background-color: rgb(178, 192, 214);
  border: solid 1px black;
  padding: 0.2em;
  box-shadow: 0.1em 0.12em rgba(35, 43, 56, 0.55);
}

select, p, b, input, button, label {
  font-size: 1.1em;
}

button:hover {
  background-color: rgb(156, 168, 187);
}

input {
  background-color: rgba(248, 248, 248, 0.87);
  border: solid 1px black;
}

select {
  background-color: transparent;
  border: 0;
}

#footer {
  padding-block: 1em;
  font-size: 0.6em;
  text-align: center;
}

#footer img {
  padding-right: 1em;
}